
import { computed, defineComponent } from 'vue';
import { Todo } from '../../types/todo';
export default defineComponent({
    name: "item",
    props:{
        todo: {
            type: Object as ()=> Todo,
            required: true
        },
        deleteTodo:{
            type: Function,
            required: true
        },
        changeTodo:{
            type: Function,
            required: true
        }
    },
    setup(props) {
        // 删除todo
        const deledte = (id: number)=>{
            props.deleteTodo(id);
        }
        const isDone = computed({
            get: ()=> props.todo.isDone,
            set: (value)=>{
                props.changeTodo(props.todo.id, value);
            }
        })
        return {
            deledte,
            isDone
        }
    }
})
