
import { defineComponent, ref, reactive } from 'vue';
import { Todo } from "@/types/todo"
export default defineComponent({
    name: "headers",
    props: {
        addTodo: {
            type: Function,
            required: true
        }
    },
    setup(props) {
        const title = ref("")
        const add = ()=>{
            if(title.value){
                const todo:Todo = {
                    id: Date.now(),
                    title: title.value,
                    isDone: false
                }
                props.addTodo(todo)
                // 重置一下
                title.value = ""
            }else{
                console.log("请输入内容");
            }
        }
        return {
            title,
            add
        }
    }
})
